import React, { useEffect, useState } from 'react';

import '../Alert.css'; // Import custom styles for transition effects

export default function Alert(props) {
    const [showAlert, setShowAlert] = useState(false);

    useEffect(() => {
        if (props.alert) {
            setShowAlert(true); // Show the alert when the props change
            const timer = setTimeout(() => {
                setShowAlert(false); // Hide the alert after 1 second (or any time you prefer)
            }, 1000); // Customize duration here (3 seconds fade)
            return () => clearTimeout(timer); // Clean up the timer on unmount
        }
    }, [props.alert]);

    return (
        <div style={{ height: '60px' }}>
            <div className={`alert-container ${showAlert ? 'show' : ''}`}>
                {props.alert && (
                    <div className={`alert alert-${props.alert.type} alert-dismissible fade show`} role="alert">
                        <center><strong>{props.alert.msg}</strong></center>
                    </div>
                )}
            </div>
        </div>
    );
}
