import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMoon, faSun } from '@fortawesome/free-solid-svg-icons';

export default function Navbar(props) {
    // console.log(props);
    
    const mode = props.mode;  // Keep mode as it is, don't modify the prop directly
    const bg = mode === 'light' ? 'info' : 'dark';
    // const btnText = mode === 'light' ? 'Enable Dark Mode' : 'Enable Light Mode';

    return (
        <>
            <div>
                <nav className={`navbar navbar-${mode} bg-${bg}`}>
                    <div className="container-fluid">
                        <a className="navbar-brand">{props.appName}</a>
                        <button onClick={props.toggleMode} className="btn btn-outline-secondary">
                            {mode === 'light' ? 
                                <FontAwesomeIcon icon={faMoon} /> : 
                                <FontAwesomeIcon icon={faSun} />
                            }
                            {/* {` ${btnText}`} */}
                        </button>  
                    </div>
                </nav>
            </div>
        </>
    );
}
