import React, { useState } from 'react';
import { Routes, Route, Navigate} from "react-router-dom";
import Alert from './components/Alert';
// import About from './components/About';
import Navbar from './components/Navbar';
import TextForm from './components/TextForm';
function App() {
  const [mode, setMode] = useState('light'); //hook for there

  const [alert, setAlert] = useState(false); //hook for alert pop-up

  const [button, setButton] = useState("info") //hook for buttons as per theme 

  const showAlert = (message, type) => {
    setAlert({
      msg: message,
      type: type
    });
    // setTimeout(() => {
    //   setAlert(null)  //`false` will also work in place of `null`
    // }, 2000);
  }

  const toggleMode = () => {
    if (mode === 'light') {
      setMode('dark');
      document.body.style.backgroundColor = 'grey';
      showAlert("Dark mode enabled", "dark");
      setButton("dark");
    }
    else {
      setMode('light');
      document.body.style.backgroundColor = 'white';
      showAlert("Light mode enabled", "info", "primary");
      setButton("info");
    }
  }

  return (
    <>
      <Navbar appName='SpeakWrite' mode={mode} toggleMode={toggleMode} />
      <Alert alert={alert} />
      {/* <TextForm heading='Enter text to analyse' mode={mode} btn={button} showAlert={showAlert} /> */}
      <Routes>
        <Route path="/" element={<Navigate to="/speakWrite" />} />
        <Route path="/speakWrite" element={<TextForm heading='Spoken Text' mode={mode} btn={button} showAlert={showAlert} />} />
        <Route path="/" element={<TextForm heading='Spoken Text' mode={mode} btn={button} showAlert={showAlert} />} />
        {/* <Route path='/about' element={<About />} /> */}
        {/* <Route path = "/" element =  */}
      </Routes>
    </>
  );
}

export default App;
